.nav-page {
  position: fixed;
  top: 0;
  left: 0;
  background-color: #e8eff9;
  height: 100vh;
  width: 100%;

  transform: translateX(100%);
  opacity: 0;
  transition:
    transform 0.5s ease,
    opacity 0.3s linear;

  &__content {
    display: flex;
    flex-direction: column;
    gap: 32px;

    @include onTablet {
      gap: 56px;
    }
  }

  &:target {
    transform: translateX(0%);
    opacity: 1;
  }
}
